body {
  height: 100vh;
  width: 100vw;
}

.signup-form > input {
  border-radius: 5px;
  border: 1px solid gray;
  font-size: 1.6rem;
}

.signup-form > label {
  font-size: 1.6rem;
}

.signup-header > h1 {
  font-size: 4rem;
}

.signup-header {
  display: flex;
  justify-content: center;
  padding: 40px 0px;
}

.signup-form {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 523px;
  margin: 10px 20px;
}

#image-upload {
  border: none;
}

.image-label {
  margin: 15px 0px 0px 0px;
}

.existing-account > h4 {
  font-size: 1.7rem;
}

.existing-account h5 {
  font-size: 1.6rem;
}

.existing-account {
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin: 20px auto;
}

@media (min-width: 768px) {
  .signup-form {
    margin: 10px 53px;
  }
  .sign-up-body {
    margin: 20px 100px;
  }
}

@media (min-width: 1024px) {
  .sign-up-body {
    margin: 20px 155px;
  }
}

@media (min-width: 1440px) {
  .sign-up-body {
    margin: 20px 300px;
  }
}
